<template>
    <div class="web-03" id="web_03"></div>
</template>

<script>
    import * as d3 from "d3"

    export default {
        name: 'Web03',
        props: {
            
        },
        data(){
            return {

            }
        },
        mounted(){
            // Web03
            var svgv = d3.select("div.web-03")
                .append("svg")

            var w = 500
            var h = 100

            svgv.attr("width",w)
                .attr("height",h)

            var dataset = [5, 10, 15, 20, 25]
            // var dataset1 = [7,14,21,28]

            var circulos = svgv.selectAll("circle")
                .data(dataset)
                .enter()
                .append("circle")

            circulos.attr("cx", function(dato, indice){
                    return (indice * 50) + 25
                })
                .attr("cy", h/2)
                .attr("r", function(dato){
                    return dato
                })
                .style("fill", "yellow")
                .style("stroke", "orange")
                .style("stroke-width", function(dato){
                    return dato * 0.5
                })
        }
    }
</script>